
  
  .food_section .filters_menu {
    padding: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    list-style-type: none;
    margin: 45px 0 20px 0;
  }
  
  .food_section .filters_menu li {
    padding: 7px 25px;
    cursor: pointer;
    border-radius: 25px;
  }
  
  .food_section .filters_menu li.active {
    /* background-color: #222831; */
    /* background-color: #000033;  */
    color: #ffffff;
  }
  
  .food_section .box {
    position: relative;
    margin-top: 25px;
    background-color: #ffffff;
    border-radius: 10px;
    color: #ffffff;
    border-radius: 15px;
    overflow: hidden;
    background: linear-gradient(to bottom, #f1f2f3 25px, #222831 25px);
  }
  
  .food_section .box .img-box {
    background: #f1f2f3;
    /* background: #ffff; */

    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    /* height: 215px; */
    height: 170px;

    border-radius: 0 0 0 45px;
    margin: -1px;
    /* padding: 25px; */
    padding: 10px;

  }
  
  .food_section .box .img-box img {
    /* max-width: 100%;
    max-height: 145px; */
    -webkit-transition: all .2s;
    transition: all .2s;
  }
  
  .food_section .box .detail-box {
    padding: 25px;
  }
  
  .food_section .box .detail-box h5 {
    font-weight: 600;
  }
  
  .food_section .box .detail-box p {
    font-size: 15px;
  }
  
  .food_section .box .detail-box h6 {
    margin-top: 10px;
  }
  
  .food_section .box .options {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
  }
  
  .food_section .box .options a {
    /* width: 40px; */
    height: 40px;
    border-radius: 100%;
    background: #ffbe33;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
  }
  
  .food_section .box .options a svg {
    /* width: 18px; */
    height: auto;
    fill: #ffffff;
  }
  
  .food_section .box:hover .img-box img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  
  .food_section .btn-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    margin-top: 45px;
  }
  
